@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,600,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    box-sizing: border-box;
}

body {
    background-color: #4e657a;
    font-family: 'Poppins', 'Helvetica', sans-serif;
    color: white;
}

a {
    text-decoration: none;
}

.container {
    max-width: 1140px;
    width: 100%;
    margin: 0 auto;
    padding: 0 20px;
}

main {
    margin-top: 50px;
}

::-webkit-scrollbar {
    width: 8px;
    height: 8px;
}

::-webkit-scrollbar-track {
    background: #394f62;
}

::-webkit-scrollbar-thumb {
    background: #6d8da6;
}

::-webkit-scrollbar-thumb:hover {
    background: #90b8d8;
}

@media screen and (max-width: 800px) {
    .container {
        padding: 10px 20px;
    }

    main {
        margin-top: 100px;
    }

}
.login-page {
    position: relative;
}

.login-form {
    background-color: #435c70;
    padding: 40px;
    max-width: 470px;
    width: 100%;
    margin: 0 auto;
    text-align: center;
    color: white;
}

.login-form h2 {
    color: white;
    text-align: center;
    font-size: 1.2em;
}

.login-form div {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

.login-form span {
    margin: 10px 0;
}

.login-form input, .login-form button {
    width: 100%;
}

.login-form input {
    padding: 19px 18px;
    border-radius: 0;
    height: 50px;
    background-color: #54657d;
    color: #fff;
    border: 0;
    border-bottom: 4px solid transparent;
}

.login-form button {
    border: unset;
    outline: unset;
    padding: 13px 28px;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
    font-size: 90%;
    font-weight: 600;
    background-color: #f5a623;
    color: white;
    cursor: pointer;
    border: 2px solid transparent;
    text-transform: uppercase;
}

.login-form button:hover {
    color: #f5a623;
    background-color: transparent;
    border: 2px solid #f5a623;
}

.login-form button {
    margin-top: 1.5rem;
    margin-bottom: 15px;
}

.login-val, .pass-val {
    display: none;
    font-size: .9em;
    text-align: left;
    color: #E15D44;
}

@media screen and (max-width: 414px) {

    .login-form {
        padding: 20px;
    }

    .login-from input {
        padding: 10px;
        height: 40px;
    }

    .login-val, .pass-val {
        padding: 0;
        list-style-position: inside;
    }
}


@media screen and (max-width: 360px) {

    .login-form h2 {
        font-size: 1em;
    }

}
header {
    background-color: #567086;
    height: 95px;
}

.header-content {
    display: flex;
    align-items: center;
    justify-content: space-between;
    
    box-sizing: border-box;
}

h1 {
    text-transform: uppercase;
    margin: 0;
    color: white;
    font-size: 1.5em;
}

.top-menu div {
    text-align: center;
    display: inline-block;
    cursor: pointer;
}

.top-menu div {
    padding: 20px;
}

.nav-link + .nav-link {
    margin-left: 20px;
}

header p {
    margin: 0;
}

header a {
    color: white;
    -webkit-transition: color .2s;
    transition: color .2s;
}

.nav-link:hover {
    color: #f5a623;
}

header i {
    font-size: 1.5em;
}

header p i {
    font-size: .9em;
}

.header-content div span {
    font-weight: 600;
}

.navbar-toggler {
    display: none;
    border: 1px solid #708da8;
    box-shadow: rgba(255, 255, 255, 0.1) 0 1px 1px 2px;
    padding: 10px 15px;
    -webkit-appearance: button;
    margin-left: auto;
    margin-right: 0;
    color: white;
    background-color: #567086;
    cursor: pointer;

    -webkit-transition: all 0.2s ease;

    transition: all 0.2s ease;
}

.navbar-toggler:hover {
    border: 1px solid #f5a623;
    color:#f5a623;
}

.hidden-menu {
    width: 180px;
    position: absolute;
    padding: 0;
    top: 65px;
    right: 20px;
    z-index: 1000;
    background-color: #567086;
    text-align: center;
    -webkit-transition: .4s;
    transition: .4s;
}

.hidden-menu.show {
    height: 180px;
    box-shadow: rgba(255, 255, 255, 0.1) 0 1px 0px 1px;
    overflow: visible;
}

.hidden-menu.hide {
    height: 0;
    box-shadow: unset;
    overflow: hidden;
}

.hidden-menu>a>div {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px 0px;
}

.hidden-menu>a>div>i {
    margin-right: 10px;
}

.hidden-menu .nav-link{
    margin: 0;
    letter-spacing: 0.3px;
}

.is-active>div {
    background-color: #f5a623;
}

.is-active:hover {
    color:white;
}

@media screen and (min-width: 800px) {
    .hidden-menu {
        display: none;
    }
}

@media screen and (max-width: 800px) {

    header {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 65px;
        z-index: 100;
    }

    .top-menu, .logout {
        display: none;
    }

    .navbar-toggler, .hidden-menu .logout {
        display: block;
    }
}

footer {
    background-color: #567086;
    color: white;
    text-align: center;
    padding: 30px 0;
    margin-top: 40px;
    box-shadow: 0 -3px 5px 0 rgba(69, 92, 113, 0.59);
    font-size: 80%;
    font-weight: 400;
}

footer p {
    margin: 0;
}

.charts {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
}

.charts>div {
    position: relative;
    margin-bottom: 20px;
    min-height: 350px;
}

.latest-hits, .performance, .storage, .notification {
    padding-left: 15px;
    padding-right: 15px;
    max-width: 50%;
    width: 100%;
}

.latest-hits>div, .performance>div, .storage>div, .notification>div {
    padding: 40px;
    box-shadow: 1px 1px 5px 0#455c71;
    max-height: 450px;
    background-color:#435c70;
}

.dashboard h2 {
    margin-top: 0;
    font-size: 1.1rem;
}

.greeting {
    margin-top: 0;
    margin-bottom: 40px;
    padding-left: 15px;
}

.order {
    width: 100%;
    max-width: 100%;
    margin-left: 15px;
    margin-right: 15px;
}

.order>div {
    padding: 30px;
    box-shadow: 1px 1px 5px 0 #455c71;
    background-color: #435c70;
    height: 100%;
}

.order table td, .order table th {
    min-width: 120px;
}

.order-table-wrapper {
    max-height: 415px;
    overflow-y: scroll;
    overflow-x: auto;
    width: 100%;
}

.notification>div {
    height: 412px;
    overflow: hidden;
}

.notification-block {
    overflow-y: scroll;
    height: 90%;
}

.notification-item {
    padding: 8px;
    background-color: #4e657a;
    color: #fff;
    font-size: 80%;
    display: flex;
}

.notification-item + .notification-item {
    margin-top: 15px;
}

.notification-thumbnail>img {
    object-fit: cover;
    width: 80px;
    height: 80px;
    border-radius: 50%;
}

.notification-desc {
    padding: 0 10px;
}

.notification-desc>p {
    margin-bottom: .5rem;
    margin-top: 5px;
}

.notification-desc>span {
    font-size: 90%;
    color: #bdcbd8;
}

.table {
    background-color: #50697f;
    color: #fff;
    font-size: 85%;
    margin-bottom: 0;
    border-collapse: collapse;
    width: 100%;
}

thead {
    background-color: #486177;
    color: #fff;
}

.order .table thead th {
    border-bottom: 0;
    vertical-align: bottom;
}

.order .table th, .order .table td {
    border-top: 1px solid #415a70;
    padding: 15px 0;
    text-align: center;
}

.moving {
    background-color: #9be64d;
    box-shadow: 0 0 8px #9be64d, inset 0 0 8px #9be64d;
}

.pending {
    background-color: #efc54b;
    box-shadow: 0 0 8px #efc54b, inset 0 0 8px #efc54b;
}

.cancelled {
    background-color: #da534f;
    box-shadow: 0 0 8px #da534f, inset 0 0 8px #da534f;
}

.delivered {
    background-color: #3889fc;
    box-shadow: 0 0 8px #3889fc, inset 0 0 8px #3889fc;
}

.tm-status-circle {
    display: inline-block;
    margin-right: 5px;
    vertical-align: middle;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    margin-top: -3px;
}

@media screen and (max-width: 1199px) {
    .greeting {
        text-align: center;
        padding-left: 0;
    }

    .charts {
        flex-direction: column;
    }

    .order {
        margin-left: 0;
        margin-right: 0;
    }

    .latest-hits, .performance, .storage, .notification {
        width: 100%;
        max-width: 60%;
        padding-left: 0;
        padding-right: 0;
    }

    .latest-hits>div, .performance>div, .storage>div, .notification>div {
        max-height: -webkit-max-content;
        max-height: -moz-max-content;
        max-height: max-content;
    }

}

@media screen and (max-width: 992px) {

    .latest-hits, .performance, .storage, .notification {
        max-width: 70%;
    }

}

@media screen and (max-width: 768px) {

    .latest-hits, .performance, .storage, .notification {
        max-width: 100%;
    }

}
.product-content {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.product-block, .category-block {
    margin-bottom: 30px;
    padding-left: 15px;
    padding-right: 15px;
}

.product-block {
    width: 70%;
}

.category-block {
    width: 30%;
}

.category-content {
    background-color: #435c70;
    padding: 40px;
    box-shadow: 1px 1px 5px 0 #455c71;
    min-height: 350px;
}

.product-table-container {
    min-height: 350px;
    padding: 40px;
    box-shadow: 1px 1px 5px 0 #455c71;
    background-color: #435c70;
}

.product-table-wrapper {
    max-height: 435px;
    overflow-y: scroll;
    overflow-x: auto;
    width: 100%;
}

.table {
    background-color: #50697f;
    color: #fff;
    margin-bottom: 0;
    width: 100%;
}

.product-table-container table th, .product-table-container table td {
    width: calc(100%/6);
}

input {
    outline: unset;
}

input[type="checkbox"] {
    cursor: pointer;
    -webkit-appearance: none;
    -moz-appearance: none;
         appearance: none;
    background-color: #394e64;
    background-position: center;
    border-radius: 50%;
    box-sizing: border-box;
    position: relative;
    box-sizing: content-box;
    width: 24px;
    height: 24px;
    -webkit-transition: all 0.1s linear;
    transition: all 0.1s linear;
}

.tm-product-delete-link {
    padding: 10px;
    border-radius: 50%;
    background-color: #394e64;
    display: inline-block;
    width: 40px;
    height: 40px;
    text-align: center;
}

.tm-product-delete-icon {
    font-size: 1.1rem;
    color: #fff;
}

.tm-product-name {
    font-size: 0.95rem;
    font-weight: 600;
}

.table-hover tbody tr {
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    cursor: pointer;
}

.btn:not(:disabled):not(.disabled) {
    cursor: pointer;
}

.btn-primary {
    color: #fff;
    background-color: #f5a623;
    border: 2px solid #f5a623;
    font-size: 90%;
    font-weight: 600;
}

.btn {
    border-radius: 0;
    padding: 13px 28px;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
    max-width: 100%;
    text-align: center;
}

.text-uppercase {
    text-transform: uppercase;
}

.btn-block {
    display: block;
    width: 100%;
}

.mb-3, .my-3 {
    margin-bottom: 1rem;
}

.btn-primary:active, .btn-primary:hover {
    color: #f5a623;
    background-color: transparent;
    border: 2px solid #f5a623;
}

.tm-block-title {
    font-size: 1.1rem;
    font-weight: 700;
    color: #fff;
    margin-bottom: 20px;
    margin-top: 0;
}

.category-table-container {
    max-height: 310px;
    margin-bottom: 15px;
    overflow-y: scroll;
}

.product-block .table th, .product-block .table td {
    padding: 18px 5px;
    height: 80px;
}

.category-block .table th, .category-block .table td {
    padding: 18px 10px;
}

.mainRow {
    padding: 0 !important;
    border: 1px solid #415a70;
    width: 100%;
}

.tableDesc {
    border: 1px solid #415a70;
}

.data-table {
    text-align: center;
}

.data-table>tbody>tr, .category-table>tbody>tr {
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
    cursor: pointer;
}

.data-table>tbody>tr:hover, .category-table>tbody>tr:hover{
    background-color: rgba(0,0,0,.075);
}

.data-table>tr>td>table {
    width: 100%;
}

.collapsed {
    border-collapse: collapse;
    border-top: 1px solid #415a70;
}

.inputCheck, .deleteIcon {
    border: 1px solid #415a70;
    border-bottom: unset;
    -webkit-transition: opacity .2s;
    transition: opacity .2s;
    width: 50px !important;
    padding: 0 !important;
}

.topRow>td, .bottomRow>td {
    border-bottom: unset;
}

.topRow {
    border-bottom: 1px solid #415a70;
}

.inputCheck:hover, .deleteIcon:hover {
    background-color: rgba(67,92,112, 0.7)
}

.product-table-container>table>tbody>tr>th {
    font-size: 100%;
    padding: 15px 18px 15px 0px;
}

.product-table-container>table>tbody>tr>th + th {
    padding-left: 15px;
}

.product-table-container>table>tbody>tr>th:first-of-type,
.product-table-container>table>tbody>tr>th:last-of-type {
    width: 50px;
}

@media screen and (max-width: 1199px) {
    .product-block{
        width: 100%;
    }

    .category-block {
        width: 100%;
    }

    .product-content {
        flex-direction: column;
        justify-content: center;
    }

    .product-table-wrapper>div>table, .data-table {
        min-width: 1000px;
    }

    .category-block .table th, .category-block .table td {
        padding: 18px 50px;
    }

}

@media screen and (max-width: 600px) {
    .category-content, .product-table-container {
        padding: 20px;
    }

    .category-block .table th, .category-block .table td {
        padding: 18px 10px 18px 15px;
    }
}
.add-category-popUp {
    display: none;
    width: 300px;
    height: 200px;
    text-align: center;
    position: fixed;
    border-radius: 10px;
    left: 50%;
    padding: 20px;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: #394f62;
    box-shadow: 1px 1px 4px 0 #455c71;
    z-index: 5;

    align-items: center;
    flex-direction: column;
    justify-content: space-around;
}

.fa-times-circle {
    position: absolute;
    top: 4%;
    right: 2%;
    color: white;
    cursor: pointer;
}

.overlay {
    position: fixed;
    display: none;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 2;
    cursor: pointer;
}
.add-product-page {
    background-color: #435c70;
    padding: 40px;
    box-shadow: 1px 1px 5px 0 #455c71;
    max-width: 850px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 20px;
    margin-bottom: 3rem;
}

.add-product-blocks {
    display: flex;
    flex-wrap: wrap;
}

.form-group label {
    color: #fff;
    margin-bottom: 10px;
}

.add-product-blocks .form-group {
    width: 100%;
    padding: 0;
}

label {
    display: inline-block;
}

.form-control {
    display: block;
    width: 100%;
    height: calc(2.25rem + 2px);
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    -webkit-transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    background-color: #54657d;
    color: #fff;
    border: 0;
    padding: 10px 18px;
    border-radius: 0;
}

textarea.form-control {
    height: auto;
}

.custom-select {
    height: 50px;
    border-radius: 0;
}

.custom-select {
    width: 100%;
    border: none;
    color: #acc6de;
    height: 50px;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    border-radius: 0;
    padding: 0 15px;
    font-size: 1.2em;
    background: url(data:image/png;base64,) 98% no-repeat #50657b;
}

.custom-control-label::before, .custom-file-label, .custom-select {
    -webkit-transition: background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    transition: background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.tm-product-img-dummy {
    max-width: 100%;
    height: 240px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    background: #aaa;
}

.tm-upload-icon {
    background: #455c71;
    width: 55px;
    height: 55px;
    border-radius: 50%;
    text-align: center;
    padding-top: 15px;
    font-size: 22px;
    cursor: pointer;
}

.custom-file {
    position: relative;
    display: inline-block;
    width: 100%;
    height: calc(2.25rem + 2px);
    margin-bottom: 0;
    margin-top: 20px;
}

.upload-image, .tm-edit-product-form {
    width: 50%;
    padding: 0 20px;
}

.add-product-page .tm-block-title {
    font-size: 1.4rem;
    padding-left: 20px;
}

.expire-stock {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.expire-stock .form-group + .form-group {
    padding-left: 10px;
}

input[type=date] {
    cursor: pointer;
}

@media screen and (max-width: 800px) {

    .add-product-blocks {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .upload-image, .tm-edit-product-form {
        width: 100%;
        padding: 0;
    }

    .add-product-page .tm-block-title {
        padding-left: 0;
    }
}

@media screen and (max-width: 414px) {
    .expire-stock {
        flex-wrap: wrap;
        justify-content: center;
    }

    .expire-stock .form-group + .form-group {
        padding-left: 0;
    }

}
.accounts-content {
    background-color: #435c70;
    padding: 40px;
}

.tm-content-row {
    margin-top: 30px;
}

.tm-content-row {
    justify-content: space-between;
    display: flex;
    flex-wrap: wrap;
}

.tm-col-avatar {
    max-width: 425px;
    width: 37%;
}

.tm-block-avatar {
    height: auto;
}

.tm-block-avatar, .tm-block-settings {
    max-height: none;
    box-shadow: 1px 1px 5px 0 #455c71;
}

.tm-bg-primary-dark {
    background-color: #435c70;
}

.tm-block {
    padding: 40px;
    box-shadow: 1px 1px 5px 0 #455c71;
    min-height: 350px;
    height: auto;
}

.tm-avatar-container {
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    flex-direction: column;
}

.tm-avatar {
    width: 345px;
}

.img-fluid {
    max-width: 100%;
    height: auto;
}

img {
    vertical-align: middle;
    border-style: none;
}

.tm-avatar-delete-link {
    position: absolute;
    left: 50%;
    top: 50%;
    margin-left: -25px;
    margin-top: -25px;
    z-index: 1000;
    padding: 14px;
    border-radius: 50%;
    background-color: rgba(57, 78, 100, 0.7);
    display: inline-block;
    width: 50px;
    height: 50px;
    text-align: center;
    display: none;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
}

.tm-product-delete-icon {
    font-size: 1.1rem;
    color: #fff;
}

.tm-avatar-container:hover .tm-avatar-delete-link {
    display: block;
}

.tm-col-account-settings {
    max-width: 822px;
    width: 60%;
}

.tm-signup-form {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.form-group {
    margin-bottom: 15px;
    width: 50%;
    padding: 5px 10px;
}

.form-group label {
    color: #fff;
    margin-bottom: 10px;
}

.form-control {
    display: block;
    width: 100%;
    font-size: 1rem;
    line-height: 1.5;
    background-clip: padding-box;
    -webkit-transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    border: 0;
    padding: 10px 18px;
    border-radius: 0;
    height: 50px;
}

.mb-4, .my-4 {
    margin-bottom: 1.5rem!important;
}

.col-12 {
    flex: 0 0 100%;
    max-width: 100%;
}

.confirm-popUp {
    display: none;
    width: 300px;
    text-align: center;
    position: fixed;
    border-radius: 10px;
    left: 50%;
    padding: 20px;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    background-color: #394f62;
    box-shadow: 1px 1px 4px 0 #455c71;
    z-index: 5;

    align-items: center;
    flex-direction: column;
    justify-content: space-around;
}

.confirm-popUp h2 {
    font-size: 1.2em;
}

.confirm-popUp button{
    outline: unset;
    border: unset;
    border-radius: 5px;
    font-size: 1.2em;
    cursor: pointer;

    color:#fff;
    background-color:#f5a623;
    border: 2px solid#f5a623;
    font-weight: 600;
    padding: 5px 28px;
    -webkit-transition: all 0.2s ease;
    transition: all 0.2s ease;
    width: 100%;
    text-align: center;
}

.confirm-popUp button:hover {
    color:#f5a623;
    background-color: transparent;
    border: 2px solid#f5a623;
}

@media screen and (max-width: 991px) {
    .tm-content-row {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .tm-col-account-settings {
        width: 100%;
        margin-top: 30px;
    }

    .tm-col-avatar {
        width: 100%;
    }

}

@media screen and (max-width: 700px) {
    .tm-signup-form {
        flex-direction: column;
    }

    .tm-signup-form .form-group {
        width: 100%;
    }
    
    .tm-hide-sm {
        display: none;
    }

}

@media screen and (max-width: 500px) {

    .tm-block {
        padding: 40px 20px;
    }
    
    .form-group button {
        font-size: .8em
    }
}
